

/////////////////////just type English in email input////////////////////
$(function() {

    var ctrlDown = false,
        ctrlA = false
        ctrlKey = 17,
        cmdKey = 91,
        vKey = 86,
        cKey = 67;
        aKey = 65,
        selectAll = false;

    var $kd = $('#email');
    var $kdc = $('#email_confirmation');
    var _to_ascii = {
        '188': '44',
        '109': '45',
        '190': '46',
        '110': '46',
        '191': '47',
        '192': '96',
        '220': '92',
        '222': '39',
        '221': '93',
        '219': '91',
        '173': '45',
        '187': '61', //IE Key codes
        '186': '59', //IE Key codes
        '189': '45'  //IE Key codes
    }

    var shiftUps = {
        "96": "~",
        "49": "!",
        "50": "@",
        "51": "#",
        "52": "$",
        "53": "%",
        "54": "^",
        "55": "&",
        "56": "*",
        "57": "(",
        "48": ")",
        "45": "_",
        "61": "+",
        "91": "{",
        "93": "}",
        "92": "|",
        "59": ":",
        "39": "\"",
        "44": "<",
        "46": ">",
        "47": "?"
    };

    $(document).keydown(function(e) { //Allow to use ctrl+c And ctrl+v
        if (e.keyCode == ctrlKey || e.keyCode == cmdKey) {
            ctrlDown = true;
        }

    }).keyup(function(e) {
            if (e.keyCode == ctrlKey || e.keyCode == cmdKey){
                ctrlDown = false;
            }
    });

    function email(e) {
        var c = e.which;
        if (_to_ascii.hasOwnProperty(c)) {
            c = _to_ascii[c];
        }

        if (!e.shiftKey && (c >= 65 && c <= 90)) {
            c = String.fromCharCode(c + 32);
        } else if (e.shiftKey && shiftUps.hasOwnProperty(c)) {
            c = shiftUps[c];
        }
        else if (96 <= c && c <= 105) {
            c = String.fromCharCode(c - 48);
        }
        else {
            c = String.fromCharCode(c);
        }

        $kd.val($kd.val()+c);
    }



    $('#email').on('keydown', function(e) {
        // console.log(e.keyCode);
        if (ctrlDown && (e.keyCode == vKey || e.keyCode == cKey ||e.keyCode == aKey)){ //Allow to use ctrl+c And ctrl+v And ctrl+a
            //do nothing
        }


        else if(e.keyCode < 46 || (e.keyCode > 111 && e.keyCode < 186 )){
            //do nothing
        }else{
            email(e);
        }
    }).on('keypress', function(e) {
        e.preventDefault();
    });
//todo: make it one method
    $('#email_confirmation').on('keydown', function(e) {
        if (ctrlDown && (e.keyCode == vKey || e.keyCode == cKey ||e.keyCode == aKey)){
            //do nothing
        }
        else if(e.keyCode < 46 || (e.keyCode > 111 && e.keyCode < 186 )){
            //do nothing
        }else{
            var c = e.which;

            if (_to_ascii.hasOwnProperty(c)) {
                c = _to_ascii[c];
            }

            if (!e.shiftKey && (c >= 65 && c <= 90)) {
                c = String.fromCharCode(c + 32);
            } else if (e.shiftKey && shiftUps.hasOwnProperty(c)) {
                c = shiftUps[c];
            }
            else if (96 <= c && c <= 105) {
                c = String.fromCharCode(c - 48);
            }
            else {
                c = String.fromCharCode(c);
            }

            $kdc.val($kdc.val()+c);
        }
    }).on('keypress', function(e) {
        e.preventDefault();
    });


});